import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import { useDispatch } from 'react-redux';
import { setHighlight } from '../store/actions';
import '../App.css';

const ChatInterface = ({ 
  onPdfNavigation, 
  messages, 
  onSendMessage,
  isWaitingForResponse = false
}) => {
  const [message, setMessage] = useState('');
  const messagesEndRef = useRef(null);
  const textareaRef = useRef(null);
  const dispatch = useDispatch();

  //   
  const exampleQuestions = [
    " PD-L1        ?",
    "EGFR     ()    .",
    "()   .",
    "T315I       ?",
    "         .",
    "ALK        ?",
    "        ?",
    "HER2         ."
  ];

  //   -    
  const keywordPageIndex = {
    //   ->   
    '': 12,
    'ponatinib': 12,
    '': 15,
    'dasatinib': 15,
    '': 18,
    'imatinib': 18,
    '': 22,
    'nilotinib': 22,
    '': 25,
    'osimertinib': 25,
    '': 32,
    '': 32,
    'pembrolizumab': 32,
    '': 36,
    'ipilimumab': 36,
    '': 38,
    'opdivo': 38,
    'nivolumab': 38,
    '': 25,
    'tagrisso': 25,
    
    //   ->   
    '': 8,
    '': 45,
    '': 52,
    '': 60,
    '': 65,
    '': 72,
    '': 80,
    '': 74,
    
    //  ->   
    'EGFR': 25,
    'ALK': 28,
    'HER2': 47,
    'ROS1': 30,
    'BRAF': 54,
    'PD-L1': 33,
    'T315I': 13
  };

  //     
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  //     
  useEffect(() => {
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto';
      const scrollHeight = textareaRef.current.scrollHeight;
      textareaRef.current.style.height = scrollHeight + 'px';
    }
  }, [message]);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (message.trim() && !isWaitingForResponse) {
      onSendMessage(message);
      setMessage('');
      //       
      if (textareaRef.current) {
        textareaRef.current.style.height = 'auto';
      }
    }
  };

  const handleKeyDown = (e) => {
    // Enter   Shift      
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit(e);
    }
  };

  //    
  const handleExampleClick = (question) => {
    if (!isWaitingForResponse) {
      onSendMessage(question);
    }
  };

  //   
  const handleKeywordClick = (pageNumber, keyword = '') => {
    if (pageNumber && onPdfNavigation) {
      if (keyword) {
        dispatch(setHighlight(keyword, parseInt(pageNumber, 10)));
      }
      onPdfNavigation(parseInt(pageNumber, 10));
    }
  };

  //    
  const formatMessageContent = (content) => {
    //      :
    // 1. [p.12], [ 12], [12] 
    // 2.  12, 12, p.12 
    // 3. : 12 
    // 4.          
    
    //    
    let formattedContent = content.replace(/\[(?:p\.|\s*\s*|\s*page\s*)?(\d+)(?:\s*)?\]/gi, 
      (match, pageNum) => {
        return `<span class="page-reference clickable-page" data-page="${pageNum}">[${pageNum}]</span>`;
      }
    );
    
    // ' '  ' '  
    formattedContent = formattedContent.replace(/(?:|page)[\s:]*([\d]+)/gi, 
      (match, pageNum) => {
        return `<span class="page-reference clickable-page" data-page="${pageNum}"> ${pageNum}</span>`;
      }
    );
    
    formattedContent = formattedContent.replace(/([\d]+)[\s]*(?:|page)/gi, 
      (match, pageNum) => {
        return `<span class="page-reference clickable-page" data-page="${pageNum}">${pageNum} </span>`;
      }
    );
    
    // ' : , '  
    formattedContent = formattedContent.replace(/\s*:?\s*([\d,\s]+)/gi, 
      (match, pageList) => {
        //      
        const processedPageList = pageList
          .split(/,\s*/)
          .map(pageNum => 
            `<span class="page-reference clickable-page" data-page="${pageNum.trim()}">${pageNum.trim()}</span>`
          )
          .join(', ');
        
        return ` : ${processedPageList}`;
      }
    );

    //   -   , ,  
    const medicalTerms = [
      //  
      '', '', '', '', '', '', '', '', '', '',
      // 
      '', '', '', '', '', '', 
      '', '', '', '', '', 'cisplatin', 'paclitaxel',
      '', '', '', '',
      // 
      '', '', '', '', '', '',
      // 
      'EGFR', 'ALK', 'ROS1', 'BRAF', 'HER2', 'PD-L1', 'KRAS', 'NTRK', 'T315I'
    ];

    //     
    const medicalTermsPattern = new RegExp(`\\b(${medicalTerms.join('|')})\\b`, 'g');
    
    //    
    formattedContent = formattedContent.replace(medicalTermsPattern, (match) => {
      return `<span class="keyword-highlight">${match}</span>`;
    });

    return formattedContent;
  };

  //   
  const renderMessageContent = (content, role) => {
    if (role === 'assistant') {
      const formattedContent = formatMessageContent(content);
      return (
        <div 
          className="message-text"
          dangerouslySetInnerHTML={{ __html: formattedContent }}
          onClick={(e) => {
            //       
            if (e.target.classList.contains('clickable-page') || e.target.className === 'page-reference') {
              const pageNumber = e.target.getAttribute('data-page');
              if (pageNumber) {
                // PDF  
                handleKeywordClick(pageNumber);
                
                //    
                e.target.classList.add('page-clicked');
                setTimeout(() => {
                  e.target.classList.remove('page-clicked');
                }, 500);
              }
            }
            //      
            else if (e.target.className === 'keyword-highlight') {
              const keyword = e.target.textContent;
              const pageNumber = keywordPageIndex[keyword];
              if (pageNumber) {
                handleKeywordClick(pageNumber, keyword);
                
                //    
                e.target.classList.add('keyword-clicked');
                setTimeout(() => {
                  e.target.classList.remove('keyword-clicked');
                }, 500);
              }
            }
          }}
        />
      );
    } else {
      return <div className="message-text">{content}</div>;
    }
  };

  //      
  const renderWelcomeMessage = () => {
    return (
      <div className="welcome-message">
        <h2>SNUH-HARI   </h2>
        <p>   .  , ,       .</p>
        <div className="example-questions-container">
          <h3>   :</h3>
          <div className="example-questions">
            {exampleQuestions.map((question, index) => (
              <div 
                key={index} 
                className="example-question"
                onClick={() => handleExampleClick(question)}
              >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                  <circle cx="12" cy="12" r="10"></circle>
                  <path d="M12 16v-4M12 8h.01"></path>
                </svg>
                {question}
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className="chat-interface">
      <div className="chat-header">
        <h2>SNUH-HARI Anti-cancer Tx Agents</h2>
        <p className="subtitle">  </p>
      </div>
      
      <div className="messages-container">
        {messages.length === 0 ? (
          renderWelcomeMessage()
        ) : (
          messages.map((msg, index) => (
            <div key={index} className={`message ${msg.role}`}>
              <div className="message-avatar">
                {msg.role === 'user' ? 
                  <span className="user-avatar"></span> : 
                  <span className="bot-avatar">HARI</span>
                }
              </div>
              {renderMessageContent(msg.content, msg.role)}
            </div>
          ))
        )}
        {isWaitingForResponse && (
          <div className="message assistant">
            <div className="message-avatar">
              <span className="bot-avatar">HARI</span>
            </div>
            <div className="message-text typing-indicator">
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
        )}
        <div ref={messagesEndRef} />
      </div>
      
      <form className="message-form" onSubmit={handleSubmit}>
        <textarea
          ref={textareaRef}
          className="message-input"
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          onKeyDown={handleKeyDown}
          placeholder=" ..."
          rows={1}
          disabled={isWaitingForResponse}
        />
        <button 
          type="submit" 
          className="send-button"
          disabled={!message.trim() || isWaitingForResponse}
        >
          <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
            <path d="M22 2L11 13"></path>
            <path d="M22 2L15 22L11 13L2 9L22 2Z"></path>
          </svg>
        </button>
      </form>
    </div>
  );
};

export default ChatInterface; 